const getPearsonCorrelation = require('./calc_correlation')

const price_sensitive_market = (tab, maps) => {
    let myMap = new Map();
    //console.log("all list is ", alllist)

    for (let [key, value] of maps) {
        content = value.get('all')
        price = content[1]
        fx = content[2]
        correlation = getPearsonCorrelation(price, fx)
        country_list = content[4]
        myMap.set(key, [correlation, country_list])
    }
    

    return
}

module.exports = price_sensitive_market; 